.CardCss {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 2em;
}

.card {
    margin: 1rem;
    --bs-card-color: var(--darkorange);
    --bs-card-border-color: var(--darkgreen)
}

@media (max-width: 768px) {
    .CardCss .card {
        width: 9rem !important;
    }

    .CardCss .card-body {
        padding: .5rem;
    }

    .CardCss .card-text {
        font-size: .8em;
    }

    .CardCss .card h5 {
        font-size: 1rem;
    }
}